<template>

    <div class="tophat">
        <v-container fluid>
            <v-row>
                <v-col cols="8" offset="2">
                    <h2>Tomb of Annihilation HexCrawl Generator</h2>
                    <br>
                    <h4>Options:</h4>
                    <v-container fluid>
                        <v-row>
                            <v-col cols="1">
                                <v-text-field type="text" label="RNG Seed" v-model="seed"></v-text-field>
                            </v-col>
                            <v-col cols="3">
                                <v-combobox label="Encounter on .. (ToA p.193)" type ="text" :items="encounterThresholdOptions" attach v-model="encounterThreshold"></v-combobox>
                            </v-col>
                            <v-col cols="1">
                                <v-text-field label="# of Days" v-model="numberOfDays"></v-text-field>
                            </v-col>
                            <v-col cols="3">
                                <v-btn @click="generateHexCrawl">generate</v-btn>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="8" offset="2">
                    <v-simple-table>
                        <template v-slot:default>
                            <thead>
                                <tr>
                                    <th>
                                        Day
                                    </th>
                                    <th colspan="3">
                                        Precipitation
                                    </th>
                                    <th colspan="2">
                                        Hexes Travelled
                                    </th>
                                    <th colspan="3">
                                        Encounters
                                    </th>
                                </tr>
                                <tr>
                                    <th></th>
                                    <th>early</th>
                                    <th>during</th>
                                    <th>late</th>
                                    <th>slow</th>
                                    <th>fast</th>
                                    <th>early</th>
                                    <th>during</th>
                                    <th>late</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="entry, index in hexCrawlLedger" :key="index">
                                    <td>{{ entry.dayNumber }}</td>
                                    <td>{{ entry.weatherEarly }}</td>
                                    <td>{{ entry.weatherDuring }}</td>
                                    <td>{{ entry.weatherLate }}</td>
                                    <td>{{ entry.slowSpeed }}</td>
                                    <td>{{ entry.fastSpeed }}</td>
                                    <td>{{ entry.encounterEarly }}</td>
                                    <td>{{ entry.encounterDuring }}</td>
                                    <td>{{ entry.encounterLate }}</td>
                                </tr>
                            </tbody>
                        </template>
                    </v-simple-table>
                </v-col>
            </v-row>
        </v-container>
    </div>
     
</template>

<script>

import RngWithSeed from 'seedrandom';

    export default {
        name: '',

        data: function() {
            return {
                randomGenerator: null,
                encounterThresholdOptions: [
                    {
                        text: 'on 16+',
                        value: '16',
                        disabled: false
                    },
                    {
                        text: 'on 18+',
                        value: '18',
                        disabled: false
                    },    
                ],
                seed: '666',
                encounterThreshold: null,
                numberOfDays: 100,
                hexCrawlLedger: null
            }      
        },

        created: function() {
            this.encounterThreshold = this.encounterThresholdOptions[0];
            this.generateHexCrawl();   
        },

        methods: {

            generateHexCrawl: function() {
                console.log("yo")
                this.randomGenerator = RngWithSeed(this.seed);
                let Entries = [];
                for(let i = 1; i <= this.numberOfDays; i++) {
                    let entry = {
                        dayNumber: i,
                        weatherEarly: this.determineWeather(),
                        weatherDuring: this.determineWeather(),
                        weatherLate: this.determineWeather(),
                        slowSpeed: this.getSlowSpeed(),
                        fastSpeed: this.getFastSpeed(),
                        encounterEarly: this.getEncounter(),
                        encounterDuring: this.getEncounter(),
                        encounterLate: this.getEncounter()
                    }
                    Entries.push(entry);
                }
                this.hexCrawlLedger = Entries;
            },

            determineWeather: function() {
                let roll = this.rollDice(20);
                let storm = false;

                if((roll >= 18) && (this.rollDice(4) === 2)) {
                    storm = true;
                }

                let result = "";
                if(roll < 13) {
                    result = result + "No rainfall";
                } else if (roll < 18) {
                    result = result + "Light rain";
                } else if (roll < 21) {
                    if(storm) {
                        result = "Tropical storm";
                    } else {
                        result = "Heavy rain";
                    }
                }
                return result;  
            },

            getSlowSpeed: function() {
                let roll = this.rollDice(4);
                if(roll < 3) {
                    return 0;
                } else {
                    return 1;
                }
            },

            getFastSpeed: function() {
                let roll = this.rollDice(4);
                if(roll > 2) {
                    return 2;
                } else {
                    return 1;
                }
            },

            getEncounter: function() {
                let roll = this.rollDice(20);
                let threshold = parseInt(this.encounterThreshold.value);
                if(roll >= threshold) {
                    return this.rollDice(100);
                } else {
                    return "-";
                }
            },

            rollDice: function(sides) {
                return Math.floor(this.randomGenerator()*sides + 1);
            }
        }

    }
</script>

